.row{
    margin-left: 20px;
    color: #fff;
}

.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
}
.posters::-webkit-scrollbar{
    display: none;
}

.poster{
    cursor: pointer;
    max-height: 250px;
    margin-right: 100px;
}
.poster:hover{
    transform: scale(1.2);
    transition: 0.2s;
}

.smallposter{
    cursor: pointer;
    max-height: 150px;
    margin-right: 100px;
}
.smallposter:hover{
    transform: scale(1.2);
    transition: 0.2s;
}